.container {
    width: 600px;
    margin: 0 auto;
    display: grid;
    place-items: center;
    margin-top: 5rem;
    background: rgb(66,4,53);
    background: linear-gradient(90deg, rgba(66,4,53,1) 0%, rgba(81,22,111,1) 35%, rgba(12,29,84,1) 100%);
    padding: 3rem 5rem;
    border-radius: 10px;
  }
  
  .time {
    font-size: 8rem;
    margin-bottom: 1rem;
    color: white;
  }
  
  .buttons button {
    padding: 0.8rem 2rem;
    border: none;
    margin-left: 0.2rem;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
    transition: all 300ms ease-in-out;
    transform: translateY(0);
  }
  
  .buttons button:hover {
    transform: translateY(-2px);
  }
  
  
  .start {
    background: #3ed927;
    color: white;
  }

  .pause {
    background: #d9cd27;
    color: white;
  }
  
  .pause {
    background: #e4e446;
  }
  
  .reset {
    background: #fd7171;
    color: white;
  }